@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");
:root {
  --blue: #056e88;
  --white: #e7e7ff; }

* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  box-sizing: border-box; }

select,
input {
  display: block; }

h1 {
  font-size: 48px; }

h2 {
  font-size: 32px;
  font-weight: 400; }

h3 {
  font-size: 14px;
  font-weight: 300;
  margin: 0.5rem 0; }

h4,
label {
  font-size: 12px;
  font-weight: bold; }

button {
  cursor: pointer; }

.background,
.result {
  background-color: var(--blue);
  box-shadow: 0px 4px 4px var(--white);
  border-radius: 5px;
  min-height: 15rem;
  padding: 1rem;
  margin: 2rem;
  position: relative;
  z-index: 0;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.background > * {
  color: white; }

.background-padding {
  padding: 0 2.5rem 4.5rem 2.5rem; }

.planetoid {
  position: absolute;
  width: 213px;
  height: 203px;
  left: -10.08px;
  bottom: -114px;
  background: linear-gradient(117.38deg, #e7e6ff 10.73%, rgba(231, 230, 255, 0) 84.05%);
  border-radius: 10rem;
  clip-path: inset(0px 0px 114px 10px);
  z-index: -10; }

.moon {
  position: absolute;
  width: 36.08px;
  height: 35.17px;
  left: 152.38px;
  bottom: 32.45px;
  background: var(--white);
  box-shadow: -10px 13px 3px rgba(0, 0, 0, 0.15);
  border-radius: 5rem;
  z-index: -5; }

.info-box {
  border-radius: 5px;
  border: 2px var(--blue) solid;
  height: 40px;
  box-shadow: 0px 4px 8px var(--white);
  display: flex;
  align-items: center;
  padding-left: 1rem;
  margin: 0.7rem 0; }

.info-box > p {
  color: var(--blue);
  font-size: 12px; }

.info-box__dot {
  height: 23px;
  width: 23px;
  border: 2px var(--blue) solid;
  border-radius: 50%;
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  margin-right: 0.5rem; }

.calculator {
  padding: 1rem 2.5rem; }

.form-input {
  height: 40px;
  width: 100%;
  padding-left: 0.5rem;
  font-size: 14px;
  margin-bottom: 0.7rem;
  margin-top: 0.5rem;
  background-color: white;
  border: 1px #a1a1a1 solid;
  border-radius: 5px; }

.form-input:disabled {
  color: #848484 !important;
  background-color: #e4e4e4;
  border: none;
  opacity: 0.7; }

.input-wrapper {
  position: relative; }

.input-wrapper > input {
  width: 100%;
  background-color: white;
  padding-left: 1.5rem; }

.input-wrapper > input:focus {
  outline: none !important;
  border: 1px solid #b4b3b3;
  border-radius: 5px; }

.input-wrapper:after {
  position: absolute;
  content: ".00";
  right: 2rem;
  top: 11px;
  font-size: 14px;
  font-weight: 400;
  color: #868686; }

.input-wrapper > .icon-holder:after {
  position: absolute;
  content: "$";
  left: 2%;
  top: 11px;
  font-size: 14px;
  font-weight: 400;
  color: #868686; }

.calculate-btn {
  width: 100%;
  height: 43px;
  margin-top: 0.5rem;
  background-color: var(--blue);
  color: white;
  font-weight: 300;
  font-size: 16px;
  border-radius: 5px;
  box-shadow: 0px 4px 8px var(--white);
  border: none; }

.calculator > button {
  color: var(--blue);
  text-decoration: underline;
  cursor: pointer;
  font-size: 14px;
  background-color: white;
  border: none; }

.result > h3 {
  color: white;
  text-align: left; }

.result-total {
  height: 70px;
  background-color: white;
  color: var(--blue);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  width: 100%; }

.tax-breakdowns {
  height: 48px;
  background-color: white;
  border-radius: 5px;
  border: none;
  margin-bottom: 0.5rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem; }

.tax-breakdowns__bracket {
  color: black; }

.tax-breakdowns__number {
  color: var(--blue);
  font-size: 18px; }

@media screen and (min-width: 770px) {
  .form {
    display: flex;
    flex: 0 0 100%;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap; }
  .background {
    flex: 0 0 100%;
    margin: 0;
    min-height: 10rem; }
  .background-padding {
    padding: 0 1.5rem 0rem 25% !important; }
  .calculator {
    width: 45%; }
  .result {
    min-height: 448px;
    width: 45%;
    margin: 1rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 2rem; }
  .planetoid {
    width: 220px;
    height: 203.64px;
    left: -29.08px;
    bottom: -68px;
    border-radius: 10rem;
    clip-path: inset(0px 0px 67px 28px); }
  .moon {
    width: 36.08px;
    height: 35.17px;
    left: 158.38px;
    bottom: 52.45px;
    border-radius: 5rem; } }

@media screen and (min-width: 1200px) {
  .form {
    justify-content: space-evenly; }
  .background {
    width: 35%; }
  .calculator {
    width: 40%; } }

@media screen and (min-width: 1500px) {
  .background {
    width: 30%; }
  .calculator {
    width: 35%; } }
